import { Component, Input, Output, EventEmitter, OnInit, OnChanges } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatTreeModule, MatTreeFlatDataSource, MatTreeFlattener } from '@angular/material/tree';
import { MatIconModule } from '@angular/material/icon';
import { MatButtonModule } from '@angular/material/button';
import { FlatTreeControl } from '@angular/cdk/tree';
import { IconComponent } from '../app-icon/icon.component';

export interface TreeNode {
  name: string;
  children?: TreeNode[];
  icon?: string;
  data?: any;
  expanded?: boolean;
}

interface FlatNode {
  expandable: boolean;
  name: string;
  level: number;
  icon?: string;
  data?: any;
}

@Component({
  selector: 'app-tree-structure',
  standalone: true,
  imports: [
    CommonModule,
    MatTreeModule,
    MatIconModule,
    MatButtonModule,
    IconComponent
  ],
  templateUrl: './tree-structure.component.html',
  styleUrl: './tree-structure.component.scss'
})
export class TreeStructureComponent implements OnInit, OnChanges {
  @Input() data: TreeNode[] = [];
  @Input() expandable: boolean = true;
  @Input() showIcons: boolean = true;
  
  @Output() nodeSelect = new EventEmitter<TreeNode>();

  treeControl = new FlatTreeControl<FlatNode>(
    node => node.level,
    node => node.expandable
  );

  treeFlattener = new MatTreeFlattener<TreeNode, FlatNode, FlatNode>(
    (node: TreeNode, level: number): FlatNode => ({
      expandable: !!node.children && node.children.length > 0,
      name: node.name,
      level: level,
      icon: node.icon,
      data: node.data
    }),
    (node: FlatNode) => node.level,
    (node: FlatNode) => node.expandable,
    (node: TreeNode) => node.children
  );

  dataSource = new MatTreeFlatDataSource(this.treeControl, this.treeFlattener);

  ngOnInit() {
    this.dataSource.data = this.data;
  }

  ngOnChanges(changes: any) {
    if (changes['data']) {
      this.dataSource.data = this.data;
    }
  }

  hasChild = (_: number, node: FlatNode) => node.expandable;

  onNodeClick(node: FlatNode) {
    const treeNode: TreeNode = {
      name: node.name,
      icon: node.icon,
      data: node.data
    };
    this.nodeSelect.emit(treeNode);
  }
}

