import { Component, Input, Output, EventEmitter, OnInit, OnChanges, OnDestroy, SimpleChanges, TemplateRef, ContentChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { AppButtonComponent } from '../app-button/app-button.component';

export interface CarouselItem {
  image?: string;
  title?: string;
  description?: string;
  data?: any;
}

@Component({
  selector: 'app-carousel',
  standalone: true,
  imports: [CommonModule, AppButtonComponent],
  templateUrl: './carousel.component.html',
  styleUrl: './carousel.component.scss'
})
export class CarouselComponent implements OnInit, OnChanges, OnDestroy {
  @Input() items: CarouselItem[] = [];
  @Input() height: string = '400px';
  @Input() width: string = '100%';
  @Input() autoPlay: boolean = false;
  @Input() autoPlayInterval: number = 3000;
  @Input() showIndicators: boolean = true;
  @Input() showControls: boolean = true;
  @Input() loop: boolean = true;
  
  @ContentChild('carouselItemTemplate') itemTemplate?: TemplateRef<any>;
  
  @Output() itemChange = new EventEmitter<{ index: number; item: CarouselItem }>();

  currentIndex: number = 0;
  private autoPlayTimer: any;

  ngOnInit() {
    if (this.autoPlay && this.items.length > 1) {
      this.startAutoPlay();
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['items'] && this.items.length > 0) {
      if (this.currentIndex >= this.items.length) {
        this.currentIndex = 0;
      }
    }
    if (changes['autoPlay']) {
      if (this.autoPlay) {
        this.startAutoPlay();
      } else {
        this.stopAutoPlay();
      }
    }
  }

  ngOnDestroy() {
    this.stopAutoPlay();
  }

  startAutoPlay() {
    this.stopAutoPlay();
    if (this.autoPlay && this.items.length > 1) {
      this.autoPlayTimer = setInterval(() => {
        this.next();
      }, this.autoPlayInterval);
    }
  }

  stopAutoPlay() {
    if (this.autoPlayTimer) {
      clearInterval(this.autoPlayTimer);
      this.autoPlayTimer = null;
    }
  }

  next() {
    if (this.currentIndex < this.items.length - 1) {
      this.currentIndex++;
    } else if (this.loop) {
      this.currentIndex = 0;
    }
    this.emitItemChange();
  }

  previous() {
    if (this.currentIndex > 0) {
      this.currentIndex--;
    } else if (this.loop) {
      this.currentIndex = this.items.length - 1;
    }
    this.emitItemChange();
  }

  goTo(index: number) {
    if (index >= 0 && index < this.items.length) {
      this.currentIndex = index;
      this.emitItemChange();
    }
  }

  emitItemChange() {
    if (this.items[this.currentIndex]) {
      this.itemChange.emit({
        index: this.currentIndex,
        item: this.items[this.currentIndex]
      });
    }
  }

  get currentItem(): CarouselItem | null {
    return this.items[this.currentIndex] || null;
  }
}

