import { Component, Input, Output, EventEmitter, OnInit } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatButtonToggleModule } from '@angular/material/button-toggle';
import { MatButtonModule } from '@angular/material/button';
import { AppButtonComponent } from '../app-button/app-button.component';
import { IconComponent } from '../app-icon/icon.component';

export interface ToggleButtonOption {
  value: any;
  label: string;
  icon?: string;
  disabled?: boolean;
  color?: 'primary' | 'accent' | 'warn';
}

@Component({
  selector: 'app-toggle-button',
  standalone: true,
  imports: [CommonModule, MatButtonToggleModule, MatButtonModule, AppButtonComponent, IconComponent],
  templateUrl: './toggle-button.component.html',
  styleUrl: './toggle-button.component.scss'
})
export class ToggleButtonComponent implements OnInit {
  @Input() options: ToggleButtonOption[] = [];
  @Input() value: any = null;
  @Input() multiple: boolean = false;
  @Input() appearance: 'standard' | 'legacy' = 'standard';
  @Input() vertical: boolean = false;
  
  @Output() valueChange = new EventEmitter<any>();
  @Output() buttonClick = new EventEmitter<{ value: any; option: ToggleButtonOption }>();

  selectedValues: any[] = [];

  ngOnInit() {
    if (this.multiple) {
      this.selectedValues = Array.isArray(this.value) ? [...this.value] : [];
    }
  }

  onToggleChange(value: any) {
    if (this.multiple) {
      this.selectedValues = Array.isArray(value) ? value : [value];
      this.valueChange.emit(this.selectedValues);
    } else {
      this.value = value;
      this.valueChange.emit(value);
      const option = this.options.find(opt => opt.value === value);
      if (option) {
        this.buttonClick.emit({ value, option });
      }
    }
  }

  onClick(option: ToggleButtonOption) {
    if (!option.disabled) {
      if (this.multiple) {
        const index = this.selectedValues.indexOf(option.value);
        if (index > -1) {
          this.selectedValues.splice(index, 1);
        } else {
          this.selectedValues.push(option.value);
        }
        this.valueChange.emit([...this.selectedValues]);
        this.buttonClick.emit({ value: option.value, option });
      } else {
        this.value = option.value;
        this.valueChange.emit(option.value);
        this.buttonClick.emit({ value: option.value, option });
      }
    }
  }

  isSelected(option: ToggleButtonOption): boolean {
    if (this.multiple) {
      return this.selectedValues.includes(option.value);
    }
    return this.value === option.value;
  }
}

