import { CommonModule } from '@angular/common';
import { Component, forwardRef, Input } from '@angular/core';
import { ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { IconComponent } from '../app-icon/icon.component';
import { TooltipDirective } from '../../directives/tooltip.directive';

let uniqueId = 0;

@Component({
  selector: 'app-button-checkbox',
  standalone: true,
  imports: [CommonModule, IconComponent, TooltipDirective],
  templateUrl: './button-checkbox.component.html',
  styleUrl: './button-checkbox.component.scss',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => ButtonCheckboxComponent),
      multi: true
    }
  ]
})
export class ButtonCheckboxComponent implements ControlValueAccessor {
  @Input() label: string = '';
  @Input() icon: string = '';
  @Input() id: string = `app-button-checkbox-${uniqueId++}`;
  @Input() disabled: boolean = false;

  value: boolean = false;

  private onChange: (value: any) => void = () => {};
  private onTouched: () => void = () => {};

  onInputChange(event: Event) {
    const target = event.target as HTMLInputElement;
    this.value = target.checked;
    this.onChange(this.value);
    this.onTouched();
  }

  // ControlValueAccessor
  writeValue(value: any): void {
    this.value = !!value;
  }

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState?(isDisabled: boolean): void {
    this.disabled = isDisabled;
  }
}
