import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders, HttpParams, HttpResponse } from '@angular/common/http';
import { Observable, throwError } from 'rxjs';
import { catchError, map } from 'rxjs/operators';
import { IHttpCallsOptions, IHttpCallsResponse, IHttpCallsError } from '../interfaces/http-calls.interface';

@Injectable({
  providedIn: 'root'
})
export class HttpCallsService {
  constructor(private http: HttpClient) {}

  /**
   * Send GET request
   * @param url - The URL to send the GET request to
   * @param options - Optional HTTP options
   * @returns Observable with the response data
   */
  sendGet<T = any>(url: string, options?: IHttpCallsOptions): Observable<T> {
    const httpOptions = this.prepareOptions(options);
    // Ensure observe is 'body' for simple methods unless explicitly set
    if (!options?.observe) {
      httpOptions.observe = 'body';
    }
    return this.http.get<T>(url, httpOptions).pipe(
      catchError(this.handleError)
    ) as Observable<T>;
  }

  /**
   * Send POST request
   * @param url - The URL to send the POST request to
   * @param body - The request body
   * @param options - Optional HTTP options
   * @returns Observable with the response data
   */
  sendPost<T = any>(url: string, body: any, options?: IHttpCallsOptions): Observable<T> {
    const httpOptions = this.prepareOptions(options);
    // Ensure observe is 'body' for simple methods unless explicitly set
    if (!options?.observe) {
      httpOptions.observe = 'body';
    }
    return this.http.post<T>(url, body, httpOptions).pipe(
      catchError(this.handleError)
    ) as Observable<T>;
  }

  /**
   * Send PUT request
   * @param url - The URL to send the PUT request to
   * @param body - The request body
   * @param options - Optional HTTP options
   * @returns Observable with the response data
   */
  sendPut<T = any>(url: string, body: any, options?: IHttpCallsOptions): Observable<T> {
    const httpOptions = this.prepareOptions(options);
    // Ensure observe is 'body' for simple methods unless explicitly set
    if (!options?.observe) {
      httpOptions.observe = 'body';
    }
    return this.http.put<T>(url, body, httpOptions).pipe(
      catchError(this.handleError)
    ) as Observable<T>;
  }

  /**
   * Send PATCH request
   * @param url - The URL to send the PATCH request to
   * @param body - The request body
   * @param options - Optional HTTP options
   * @returns Observable with the response data
   */
  sendPatch<T = any>(url: string, body: any, options?: IHttpCallsOptions): Observable<T> {
    const httpOptions = this.prepareOptions(options);
    // Ensure observe is 'body' for simple methods unless explicitly set
    if (!options?.observe) {
      httpOptions.observe = 'body';
    }
    return this.http.patch<T>(url, body, httpOptions).pipe(
      catchError(this.handleError)
    ) as Observable<T>;
  }

  /**
   * Send DELETE request
   * @param url - The URL to send the DELETE request to
   * @param options - Optional HTTP options
   * @returns Observable with the response data
   */
  sendDelete<T = any>(url: string, options?: IHttpCallsOptions): Observable<T> {
    const httpOptions = this.prepareOptions(options);
    // Ensure observe is 'body' for simple methods unless explicitly set
    if (!options?.observe) {
      httpOptions.observe = 'body';
    }
    return this.http.delete<T>(url, httpOptions).pipe(
      catchError(this.handleError)
    ) as Observable<T>;
  }

  /**
   * Get full HTTP response (including headers, status, etc.)
   * @param url - The URL to send the GET request to
   * @param options - Optional HTTP options
   * @returns Observable with the full HTTP response
   */
  sendGetResponse<T = any>(url: string, options?: IHttpCallsOptions): Observable<IHttpCallsResponse<T>> {
    const baseOptions = this.prepareOptions(options);
    const httpOptions: any = {
      ...baseOptions,
      observe: 'response' as const
    };
    return (this.http.get<T>(url, httpOptions) as Observable<HttpResponse<T>>).pipe(
      map((response: HttpResponse<T>) => ({
        data: response.body as T,
        status: response.status,
        statusText: response.statusText,
        headers: response.headers
      })),
      catchError(this.handleError)
    );
  }

  /**
   * Post and get full HTTP response
   * @param url - The URL to send the POST request to
   * @param body - The request body
   * @param options - Optional HTTP options
   * @returns Observable with the full HTTP response
   */
  sendPostResponse<T = any>(url: string, body: any, options?: IHttpCallsOptions): Observable<IHttpCallsResponse<T>> {
    const baseOptions = this.prepareOptions(options);
    const httpOptions: any = {
      ...baseOptions,
      observe: 'response' as const
    };
    return (this.http.post<T>(url, body, httpOptions) as Observable<HttpResponse<T>>).pipe(
      map((response: HttpResponse<T>) => ({
        data: response.body as T,
        status: response.status,
        statusText: response.statusText,
        headers: response.headers
      })),
      catchError(this.handleError)
    );
  }

  private prepareOptions(options?: IHttpCallsOptions): any {
    const defaultOptions: any = {
      headers: new HttpHeaders({
        'Content-Type': 'application/json'
      }),
      observe: 'body',
      responseType: 'json'
    };

    if (options) {
      if (options.headers) {
        defaultOptions.headers = options.headers instanceof HttpHeaders 
          ? options.headers 
          : new HttpHeaders(options.headers);
      }
      if (options.params) {
        defaultOptions.params = options.params instanceof HttpParams
          ? options.params
          : new HttpParams({ fromObject: options.params });
      }
      if (options.observe) {
        defaultOptions.observe = options.observe;
      }
      if (options.reportProgress !== undefined) {
        defaultOptions.reportProgress = options.reportProgress;
      }
      if (options.responseType) {
        defaultOptions.responseType = options.responseType;
      }
      if (options.withCredentials !== undefined) {
        defaultOptions.withCredentials = options.withCredentials;
      }
    }

    return defaultOptions;
  }

  private handleError = (error: any): Observable<never> => {
    const errorResponse: IHttpCallsError = {
      message: error.message || 'An error occurred',
      status: error.status,
      statusText: error.statusText,
      error: error.error
    };
    return throwError(() => errorResponse);
  };
}

