import { Component, Input, Output, EventEmitter } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { MatSliderModule } from '@angular/material/slider';
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';

export interface SliderPoint {
  value: number;
  label?: string;
  color?: string;
}

@Component({
  selector: 'app-slider',
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    MatSliderModule,
    MatInputModule,
    MatFormFieldModule
  ],
  templateUrl: './slider.component.html',
  styleUrl: './slider.component.scss'
})
export class SliderComponent {
  @Input() min: number = 0;
  @Input() max: number = 100;
  @Input() step: number = 1;
  @Input() value: number = 0;
  @Input() points: SliderPoint[] = [];
  @Input() showValue: boolean = true;
  @Input() showInput: boolean = false;
  @Input() disabled: boolean = false;
  @Input() vertical: boolean = false;
  @Input() color: 'primary' | 'accent' | 'warn' = 'primary';
  
  @Output() valueChange = new EventEmitter<number>();
  @Output() change = new EventEmitter<number>();

  onValueChange(event: any) {
    const newValue = typeof event === 'number' ? event : (event?.value ?? event?.target?.value ?? this.value);
    this.value = newValue;
    this.valueChange.emit(newValue);
    this.change.emit(newValue);
  }

  getPointPosition(point: SliderPoint): number {
    return ((point.value - this.min) / (this.max - this.min)) * 100;
  }
}

