import { Component, Input } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatStepperModule } from '@angular/material/stepper';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';

@Component({
  selector: 'app-stepper',
  standalone: true,
  imports: [CommonModule, MatStepperModule, FormsModule, ReactiveFormsModule],
  templateUrl: './stepper.component.html',
  styleUrl: './stepper.component.scss'
})
export class StepperComponent {
  @Input() orientation: 'horizontal' | 'vertical' = 'horizontal';
  @Input() linear: boolean = false;
  @Input() selectedIndex: number = 0;
}

@Component({
  selector: 'app-step',
  standalone: true,
  imports: [CommonModule, MatStepperModule],
  template: `
    <mat-step [label]="label" [optional]="optional" [completed]="completed" [state]="state">
      <ng-content></ng-content>
    </mat-step>
  `
})
export class StepComponent {
  @Input() label: string = '';
  @Input() optional: boolean = false;
  @Input() completed: boolean = false;
  @Input() state: 'number' | 'edit' | 'done' | 'error' = 'number';
}

