import { Component, Input, Output, EventEmitter, forwardRef } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ControlValueAccessor, NG_VALUE_ACCESSOR } from '@angular/forms';
import { AppButtonComponent } from '../app-button/app-button.component';

@Component({
  selector: 'app-rich-text-editor',
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    AppButtonComponent
  ],
  templateUrl: './rich-text-editor.component.html',
  styleUrl: './rich-text-editor.component.scss',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => RichTextEditorComponent),
      multi: true
    }
  ]
})
export class RichTextEditorComponent implements ControlValueAccessor {
  @Input() placeholder: string = 'Enter text here...';
  @Input() height: string = '300px';
  @Input() showToolbar: boolean = true;
  
  @Output() contentChange = new EventEmitter<string>();

  content: string = '';
  private onChange = (value: string) => {};
  public onTouched = () => {};

  writeValue(value: string): void {
    this.content = value || '';
    if (this.getEditorElement()) {
      this.getEditorElement()!.innerHTML = this.content;
    }
  }

  registerOnChange(fn: (value: string) => void): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: () => void): void {
    this.onTouched = fn;
  }

  onContentChange() {
    const editor = this.getEditorElement();
    if (editor) {
      this.content = editor.innerHTML;
      this.onChange(this.content);
      this.contentChange.emit(this.content);
    }
  }

  executeCommand(command: string, value: string = '') {
    document.execCommand(command, false, value);
    this.onContentChange();
    this.getEditorElement()?.focus();
  }

  getEditorElement(): HTMLElement | null {
    return document.getElementById('rich-text-editor-content');
  }

  formatText(command: string) {
    this.executeCommand(command);
  }

  insertLink() {
    const url = prompt('Enter URL:');
    if (url) {
      this.executeCommand('createLink', url);
    }
  }

  insertImage() {
    const url = prompt('Enter image URL:');
    if (url) {
      this.executeCommand('insertImage', url);
    }
  }
}

