import { Component, inject } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { AppButtonComponent } from '../app-button/app-button.component';
import { ConfirmationDialogData } from './confirmation-dialog.component';

@Component({
  selector: 'app-confirmation-dialog-wrapper',
  standalone: true,
  imports: [
    CommonModule,
    AppButtonComponent
  ],
  templateUrl: './confirmation-dialog-wrapper.component.html',
  styleUrls: ['./confirmation-dialog-wrapper.component.scss']
})
export class ConfirmationDialogWrapperComponent {
  dialogRef = inject(MatDialogRef<ConfirmationDialogWrapperComponent>);
  data = inject<ConfirmationDialogData>(MAT_DIALOG_DATA);

  onFooterActionClick(actionId: string, event?: Event) {
    if (event) {
      event.preventDefault();
      event.stopPropagation();
    }
    
    const result: any = { action: actionId, actionId };
    if (actionId === 'confirm') {
      result.confirmed = true;
    } else if (actionId === 'cancel') {
      result.confirmed = false;
    }
    
    this.dialogRef.close(result);
  }

  get displayTitle(): string {
    return this.data?.title || 'Confirm';
  }

  get displayMessage(): string {
    return this.data?.message || 'Are you sure?';
  }

  get footerActions(): any[] {
    const actions: any[] = [];
    
    if (this.data?.showCancel !== false) {
      actions.push({
        id: 'cancel',
        text: this.data?.cancelText || 'Cancel',
        color: 'primary',
        appearance: 'flat'
      });
    }

    if (this.data?.showConfirm !== false) {
      actions.push({
        id: 'confirm',
        text: this.data?.confirmText || 'Yes',
        color: 'primary',
        appearance: 'flat'
      });
    }

    return actions;
  }
}

