import { Component, Input, OnInit, OnChanges, SimpleChanges, ElementRef, ViewChild, OnDestroy } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Observable, Subscription } from 'rxjs';
import { ChartService } from '../../services/chart.service';
import { ChartOptions } from '../../interfaces/chart.interface';

@Component({
  selector: 'app-chart',
  standalone: true,
  imports: [CommonModule],
  templateUrl: './chart.component.html',
  styleUrl: './chart.component.scss'
})
export class ChartComponent implements OnInit, OnChanges, OnDestroy {
  @Input() chartOptions$!: Observable<ChartOptions>;
  @Input() chartOptions?: ChartOptions;
  @Input() height: string = '100%';
  @Input() width: string = '100%';

  @ViewChild('chartContainer', { static: true }) chartContainer!: ElementRef;

  private chartInstance: any = null;
  private subscription?: Subscription;

  constructor(private chartService: ChartService) {}

  ngOnInit() {
    if (this.chartOptions$) {
      this.subscription = this.chartOptions$.subscribe(options => {
        this.renderChart(options);
      });
    } else if (this.chartOptions) {
      this.renderChart(this.chartOptions);
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['chartOptions'] && this.chartOptions && !this.chartOptions$) {
      this.renderChart(this.chartOptions);
    }
  }

  ngOnDestroy() {
    if (this.subscription) {
      this.subscription.unsubscribe();
    }
    if (this.chartInstance) {
      this.chartInstance.destroy();
    }
  }

  private renderChart(options: ChartOptions) {
    if (this.chartContainer) {
      if (this.chartInstance) {
        this.chartInstance.destroy();
      }
      this.chartInstance = this.chartService.createChart(
        this.chartContainer.nativeElement,
        options
      );
    }
  }
}

