import { Component, Input, Output, EventEmitter, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
import { AppButtonComponent } from '../app-button/app-button.component';

@Component({
  selector: 'app-json-reflect',
  standalone: true,
  imports: [
    CommonModule,
    FormsModule,
    MatButtonModule,
    MatIconModule,
    MatInputModule,
    MatFormFieldModule,
    AppButtonComponent
  ],
  templateUrl: './json-reflect.component.html',
  styleUrl: './json-reflect.component.scss'
})
export class JsonReflectComponent implements OnInit, OnChanges {
  @Input() json: any = {};
  @Input() editable: boolean = true;
  @Input() showControls: boolean = true;
  @Input() height: string = '400px';
  
  @Output() jsonChange = new EventEmitter<any>();
  @Output() save = new EventEmitter<any>();
  @Output() cancel = new EventEmitter<void>();

  jsonString: string = '';
  isEditing: boolean = false;
  errorMessage: string = '';

  ngOnInit() {
    this.updateJsonString();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['json'] && !this.isEditing) {
      this.updateJsonString();
    }
  }

  updateJsonString() {
    try {
      this.jsonString = JSON.stringify(this.json, null, 2);
      this.errorMessage = '';
    } catch (error) {
      this.jsonString = '';
      this.errorMessage = 'Invalid JSON';
    }
  }

  toggleEdit() {
    if (this.editable) {
      this.isEditing = !this.isEditing;
      if (!this.isEditing) {
        this.updateJsonString();
      }
    }
  }

  onJsonChange() {
    try {
      const parsed = JSON.parse(this.jsonString);
      this.json = parsed;
      this.errorMessage = '';
      this.jsonChange.emit(parsed);
    } catch (error) {
      this.errorMessage = 'Invalid JSON format';
    }
  }

  onSave() {
    try {
      const parsed = JSON.parse(this.jsonString);
      this.json = parsed;
      this.errorMessage = '';
      this.isEditing = false;
      this.save.emit(parsed);
    } catch (error) {
      this.errorMessage = 'Invalid JSON format. Cannot save.';
    }
  }

  onCancel() {
    this.updateJsonString();
    this.isEditing = false;
    this.errorMessage = '';
    this.cancel.emit();
  }

  formatJson() {
    try {
      const parsed = JSON.parse(this.jsonString);
      this.jsonString = JSON.stringify(parsed, null, 2);
      this.errorMessage = '';
    } catch (error) {
      this.errorMessage = 'Invalid JSON format';
    }
  }
}

