import { Injectable } from '@angular/core';
import { ChartOptions } from '../interfaces/chart.interface';

declare var Highcharts: any;

@Injectable({
  providedIn: 'root'
})
export class ChartService {
  private defaultOptions: ChartOptions = {
    chart: {
      type: 'line'
    },
    title: {
      text: 'Chart'
    },
    credits: {
      enabled: false
    },
    legend: {
      enabled: true
    },
    tooltip: {
      enabled: true
    }
  };

  /**
   * Create a chart instance
   * @param container - The DOM element or element ID to render the chart in
   * @param options - Chart configuration options
   * @returns Highcharts chart instance
   */
  createChart(container: string | HTMLElement, options: ChartOptions): any {
    if (typeof Highcharts === 'undefined') {
      console.error('Highcharts library is not loaded. Please include Highcharts in your project.');
      return null;
    }

    const mergedOptions = this.mergeOptions(options);
    return Highcharts.chart(container, mergedOptions);
  }

  /**
   * Update an existing chart
   * @param chartInstance - The Highcharts chart instance
   * @param options - Chart configuration options to update
   */
  updateChart(chartInstance: any, options: ChartOptions): void {
    if (!chartInstance) {
      console.error('Chart instance is not provided');
      return;
    }

    const mergedOptions = this.mergeOptions(options);
    chartInstance.update(mergedOptions, true);
  }

  /**
   * Destroy a chart instance
   * @param chartInstance - The Highcharts chart instance to destroy
   */
  destroyChart(chartInstance: any): void {
    if (chartInstance && typeof chartInstance.destroy === 'function') {
      chartInstance.destroy();
    }
  }

  /**
   * Get default chart options
   * @param type - Chart type
   * @returns Default chart options for the specified type
   */
  getDefaultOptions(type: 'line' | 'bar' | 'column' | 'pie' | 'area' | 'scatter' | 'spline' | 'areaSpline' = 'line'): ChartOptions {
    return {
      ...this.defaultOptions,
      chart: {
        ...this.defaultOptions.chart,
        type: type
      }
    };
  }

  /**
   * Create chart options object with provided input
   * @param input - Partial chart options
   * @returns Complete chart options object
   */
  createChartOptions(input: Partial<ChartOptions>): ChartOptions {
    return this.mergeOptions(input);
  }

  private mergeOptions(options: ChartOptions | Partial<ChartOptions>): any {
    const merged: any = {
      ...this.defaultOptions,
      ...options
    };

    // Deep merge chart, title, and other nested objects
    if (options.chart) {
      merged.chart = {
        ...this.defaultOptions.chart,
        ...options.chart
      };
    }

    if (options.title) {
      merged.title = {
        ...this.defaultOptions.title,
        ...options.title
      };
    }

    return merged;
  }
}

