import { Component, Input, ContentChild, TemplateRef, OnChanges, SimpleChanges, OnDestroy } from '@angular/core';
import { BreadcrumbItem } from '../../interfaces/breadcrumb.interface';
import { CommonModule } from '@angular/common';
import { MatIconModule } from '@angular/material/icon';
import { MatButtonModule } from '@angular/material/button';
import { RouterModule } from '@angular/router';
import { IconComponent } from '../app-icon/icon.component';
import { PageBodyDirective } from './page-body.directive';
import { FooterDirective } from './footer.directive';
import { CoreEventService } from '../../core-event.service';

@Component({
  selector: 'app-page',
  standalone: true,
  imports: [CommonModule, MatIconModule, MatButtonModule, RouterModule, IconComponent, PageBodyDirective, FooterDirective],
  templateUrl: './page.component.html',
  styleUrl: './page.component.scss'
})
export class PageComponent implements OnChanges, OnDestroy {
  @Input() breadcrumb: BreadcrumbItem[] | null = null;
  @Input() isFooter: boolean = false;
  
  @ContentChild('pageBody') pageBodyTemplate?: TemplateRef<any>;
  @ContentChild('footer') footerTemplate?: TemplateRef<any>;

  constructor(private coreEvent: CoreEventService) {}

  ngOnChanges(changes: SimpleChanges): void {
    if ('breadcrumb' in changes) {
      this.syncBreadcrumb();
    }
  }

  ngOnDestroy(): void {
    this.coreEvent.clearBreadcrumb();
  }

  private syncBreadcrumb() {
    if (this.breadcrumb && this.breadcrumb.length > 0) {
      this.coreEvent.setBreadcrumb(this.breadcrumb);
    } else {
      this.coreEvent.clearBreadcrumb();
    }
  }
}

