import { Injectable, signal, computed, effect, Signal, Injector, runInInjectionContext } from '@angular/core';
import { BreadcrumbItem } from './interfaces/breadcrumb.interface';

/**
 * Core Event Service using Angular Signals for cross-library communication
 * This service provides a centralized event bus for the application
 */
@Injectable({ providedIn: 'root' })
export class CoreEventService {
  constructor(private injector: Injector) {}
  // Private signals for internal state management
  private _menu = signal<string | null>(null);
  private _authState = signal<{ userId: string; userType?: string; isAuthenticated: boolean } | null>(null);
  private _breadcrumb = signal<BreadcrumbItem[] | null>(null);
  private _notification = signal<{ message: string; type?: 'success' | 'error' | 'info' | 'warning' } | null>(null);

  // Public read-only computed signals
  readonly menu: Signal<string | null> = computed(() => this._menu());
  readonly authState: Signal<{ userId: string; userType?: string; isAuthenticated: boolean } | null> = computed(() => this._authState());
  readonly breadcrumb: Signal<BreadcrumbItem[] | null> = computed(() => this._breadcrumb());
  readonly notification: Signal<{ message: string; type?: 'success' | 'error' | 'info' | 'warning' } | null> = computed(() => this._notification());

  // Menu updaters
  setMenu(menu: string | null) {
    this._menu.set(menu);
  }

  clearMenu() {
    this._menu.set(null);
  }

  // Auth state updaters
  setAuth(state: { userId: string; userType?: string; isAuthenticated: boolean } | null) {
    this._authState.set(state);
  }

  clearAuth() {
    this._authState.set(null);
  }

  // Breadcrumb updaters
  setBreadcrumb(breadcrumb: BreadcrumbItem[] | BreadcrumbItem | null) {
    if (breadcrumb === null) {
      this._breadcrumb.set(null);
    } else if (Array.isArray(breadcrumb)) {
      this._breadcrumb.set(breadcrumb);
    } else {
      // Single item, convert to array
      this._breadcrumb.set([breadcrumb]);
    }
  }

  clearBreadcrumb() {
    this._breadcrumb.set(null);
  }

  // Notification updaters
  setNotification(notification: { message: string; type?: 'success' | 'error' | 'info' | 'warning' } | null) {
    this._notification.set(notification);
  }

  clearNotification() {
    this._notification.set(null);
  }

  // Convenience methods: run callback on signal changes (returns a destroy function)
  onMenuChange(cb: (menu: string | null) => void) {
    return runInInjectionContext(this.injector, () => {
      const runner = effect(() => {
        cb(this._menu());
      });
      return () => runner.destroy(); // destroy the effect to cleanup
    });
  }

  onAuthChange(cb: (auth: { userId: string; userType?: string; isAuthenticated: boolean } | null) => void) {
    return runInInjectionContext(this.injector, () => {
      const runner = effect(() => {
        cb(this._authState());
      });
      return () => runner.destroy();
    });
  }

  onBreadcrumbChange(cb: (breadcrumb: BreadcrumbItem[] | null) => void) {
    return runInInjectionContext(this.injector, () => {
      const runner = effect(() => {
        cb(this._breadcrumb());
      });
      return () => runner.destroy();
    });
  }

  onNotificationChange(cb: (notification: { message: string; type?: 'success' | 'error' | 'info' | 'warning' } | null) => void) {
    return runInInjectionContext(this.injector, () => {
      const runner = effect(() => {
        cb(this._notification());
      });
      return () => runner.destroy();
    });
  }
}

