import { Component, Input, Output, EventEmitter, inject } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { MatDialog } from '@angular/material/dialog';
import { AppButtonComponent } from '../app-button/app-button.component';
import { FilterComponent, DIALOG_DATA_TOKEN } from '../filter/filter.component';
import { DialogboxService } from '../dialogbox/dialogbox.component';

export interface GridFilterField {
  value: string;
  label: string;
  type?: 'text' | 'number' | 'date' | 'select';
  operators?: string[];
  values?: any[];
}

export interface GridFilter {
  field: string;
  operator: string;
  value: any;
}

@Component({
  selector: 'app-grid-list-filters',
  standalone: true,
  imports: [CommonModule, MatButtonModule, MatIconModule, AppButtonComponent],
  templateUrl: './grid-list-filters.component.html',
  styleUrl: './grid-list-filters.component.scss'
})
export class GridListFiltersComponent {
  @Input() fields: GridFilterField[] = [];
  @Input() operators: { label: string; value: string }[] = [];
  @Input() operatorsMap: { [key: string]: { label: string; value: string }[] } = {};
  @Input() valuesMap: { [key: string]: any[] } = {};
  @Input() title: string = 'Filter Data';
  @Input() buttonText: string = 'Filter';
  @Input() buttonIcon: string = 'filter_list';
  
  @Output() filtersApplied = new EventEmitter<GridFilter[]>();
  @Output() filtersCleared = new EventEmitter<void>();

  private dialog = inject(MatDialog);
  private dialogboxService = inject(DialogboxService);
  appliedFiltersCount: number = 0;

  openFiltersDialog() {
    const dialogRef = this.dialogboxService.openDialog(FilterComponent, {
      title: this.title,
      width: '600px',
      data: {
        fields: this.fields,
        operators: this.operators,
        operatorsMap: this.operatorsMap,
        valuesMap: this.valuesMap,
        title: this.title
      }
    });

    dialogRef.afterClosed().subscribe((result: any) => {
      if (result && result.filters) {
        this.appliedFiltersCount = result.filters.length;
        this.filtersApplied.emit(result.filters);
      }
    });
  }

  clearFilters() {
    this.appliedFiltersCount = 0;
    this.filtersCleared.emit();
  }
}

