import { Component, Input, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DomSanitizer, SafeResourceUrl } from '@angular/platform-browser';
import { MatButtonModule } from '@angular/material/button';
import { MatIconModule } from '@angular/material/icon';
import { AppButtonComponent } from '../app-button/app-button.component';

@Component({
  selector: 'app-pdf-viewer',
  standalone: true,
  imports: [CommonModule, MatButtonModule, MatIconModule, AppButtonComponent],
  templateUrl: './pdf-viewer.component.html',
  styleUrl: './pdf-viewer.component.scss'
})
export class PdfViewerComponent implements OnInit, OnChanges {
  @Input() src: string | ArrayBuffer | null = null;
  @Input() height: string = '600px';
  @Input() width: string = '100%';
  @Input() showToolbar: boolean = true;
  @Input() downloadFileName: string = 'document.pdf';

  safeSrc: SafeResourceUrl | null = null;
  pdfUrl: string = '';

  constructor(private sanitizer: DomSanitizer) {}

  ngOnInit() {
    this.updatePdfSource();
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['src']) {
      this.updatePdfSource();
    }
  }

  updatePdfSource() {
    if (!this.src) {
      this.safeSrc = null;
      this.pdfUrl = '';
      return;
    }

    if (typeof this.src === 'string') {
      this.pdfUrl = this.src;
      this.safeSrc = this.sanitizer.bypassSecurityTrustResourceUrl(this.src);
    } else if (this.src instanceof ArrayBuffer) {
      const blob = new Blob([this.src], { type: 'application/pdf' });
      const url = URL.createObjectURL(blob);
      this.pdfUrl = url;
      this.safeSrc = this.sanitizer.bypassSecurityTrustResourceUrl(url);
    }
  }

  downloadPdf() {
    if (this.pdfUrl) {
      const link = document.createElement('a');
      link.href = this.pdfUrl;
      link.download = this.downloadFileName;
      link.click();
    }
  }

  printPdf() {
    if (this.pdfUrl) {
      const printWindow = window.open(this.pdfUrl, '_blank');
      if (printWindow) {
        printWindow.onload = () => {
          printWindow.print();
        };
      }
    }
  }
}

