import { Component, OnInit, OnDestroy, inject } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DIALOG_DATA_TOKEN } from '../index';
import { Subject } from 'rxjs';

export interface ConfirmationDialogData {
  message: string;
  title?: string;
  cancelText?: string;
  confirmText?: string;
  icon?: string;
  showCancel?: boolean;
  showConfirm?: boolean;
}

@Component({
  selector: 'app-confirmation-dialog',
  standalone: true,
  imports: [
    CommonModule
  ],
  templateUrl: './confirmation-dialog.component.html',
  styleUrls: ['./confirmation-dialog.component.scss']
})
export class ConfirmationDialogComponent implements OnInit, OnDestroy {
  data = inject<ConfirmationDialogData>(DIALOG_DATA_TOKEN);
  private destroy$ = new Subject<void>();

  constructor() {}

  ngOnInit(): void {
    // Component just displays the message
    // Footer actions are handled by DialogboxContentComponent
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  get displayMessage(): string {
    return this.data?.message || 'Are you sure?';
  }

  get displayTitle(): string {
    return this.data?.title || 'Confirm';
  }
}

