import { Component, Input, Output, EventEmitter, OnInit, OnChanges, SimpleChanges } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatButtonModule } from '@angular/material/button';
import { MatInputModule } from '@angular/material/input';
import { MatFormFieldModule } from '@angular/material/form-field';
import { FormsModule } from '@angular/forms';
import { AppButtonComponent } from '../app-button/app-button.component';

@Component({
  selector: 'app-qr-code-generator',
  standalone: true,
  imports: [
    CommonModule,
    MatButtonModule,
    MatInputModule,
    MatFormFieldModule,
    FormsModule,
    AppButtonComponent
  ],
  templateUrl: './qr-code-generator.component.html',
  styleUrl: './qr-code-generator.component.scss'
})
export class QrCodeGeneratorComponent implements OnInit, OnChanges {
  @Input() data: string = '';
  @Input() size: number = 200;
  @Input() errorCorrectionLevel: 'L' | 'M' | 'Q' | 'H' = 'M';
  @Input() showInput: boolean = true;
  @Input() showDownload: boolean = true;
  
  @Output() qrGenerated = new EventEmitter<string>();

  qrCodeDataUrl: string = '';
  inputData: string = '';

  ngOnInit() {
    this.inputData = this.data;
    if (this.data) {
      this.generateQRCode();
    }
  }

  ngOnChanges(changes: SimpleChanges) {
    if (changes['data'] && this.data) {
      this.inputData = this.data;
      this.generateQRCode();
    }
  }

  generateQRCode() {
    if (!this.inputData) {
      this.qrCodeDataUrl = '';
      return;
    }

    // Using a simple QR code generation approach
    // In production, you would use a library like 'qrcode' or 'angularx-qrcode'
    // For now, we'll use a QR code API service
    const qrApiUrl = `https://api.qrserver.com/v1/create-qr-code/?size=${this.size}x${this.size}&data=${encodeURIComponent(this.inputData)}&ecc=${this.errorCorrectionLevel}`;
    this.qrCodeDataUrl = qrApiUrl;
    this.qrGenerated.emit(this.qrCodeDataUrl);
  }

  onInputChange() {
    this.generateQRCode();
  }

  downloadQRCode() {
    if (this.qrCodeDataUrl) {
      const link = document.createElement('a');
      link.href = this.qrCodeDataUrl;
      link.download = 'qrcode.png';
      link.click();
    }
  }
}

