import { Component, Input, ContentChildren, QueryList, AfterContentInit, TemplateRef, ViewChild } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MatTabsModule } from '@angular/material/tabs';

@Component({
  selector: 'app-tab',
  standalone: true,
  imports: [CommonModule, MatTabsModule],
  template: `
    <ng-template #tabContent>
      <ng-content></ng-content>
    </ng-template>
  `
})
export class TabComponent {
  @Input() label: string = '';
  @Input() disabled: boolean = false;
  @ViewChild('tabContent', { static: true }) content!: TemplateRef<unknown>;
}

@Component({
  selector: 'app-tabs',
  standalone: true,
  imports: [CommonModule, MatTabsModule, TabComponent],
  templateUrl: './tabs.component.html',
  styleUrl: './tabs.component.scss'
})
export class TabsComponent implements AfterContentInit {
  @Input() selectedIndex: number = 0;
  @Input() animationDuration: string = '500ms';
  @Input() dynamicHeight: boolean = false;
  @Input() backgroundColor?: string;
  @Input() color: 'primary' | 'accent' | 'warn' = 'primary';

  @ContentChildren(TabComponent) tabs!: QueryList<TabComponent>;

  ngAfterContentInit() {
    // Tabs are managed by content projection
  }
}

